"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation"; 
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { 
  Plus, Search, FileText, Clock, AlertCircle, Sparkles, 
  Zap, History, MousePointer2, Star
} from "lucide-react";
import Link from "next/link";
import { Skeleton } from "@/components/ui/skeleton";

// --- MOCK DATA ---
const MOCK_NOTES: Note[] = [
  { id: "note-1", title: "Project Roadmap", updatedAt: "2025-12-25", previewText: "Our primary goals for Q1 involve full LLM integration..." },
  { id: "note-2", title: "Meeting with Design", updatedAt: "2025-12-26", previewText: "Reviewing the new Shadcn glassmorphism theme." },
  { id: "note-3", title: "API Documentation", updatedAt: "2025-12-27", previewText: "Endpoints for real-time collaboration using WebSockets." },
];

type Note = {
  id: string;
  title: string;
  updatedAt: string;
  previewText?: string;
};

export default function DashboardPage() {
  const [notes, setNotes] = useState<Note[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchQuery, setSearchQuery] = useState("");
  
  const router = useRouter(); 
  const [isCreating, setIsCreating] = useState(false);

  useEffect(() => {
    async function fetchNotes() {
      try {
        setLoading(true);
        // Simulating network delay
        await new Promise((resolve) => setTimeout(resolve, 800));
        setNotes(MOCK_NOTES);
      } catch (err) {
        setError("Could not load notes.");
      } finally {
        setLoading(false);
      }
    }
    fetchNotes();
  }, []);

  const handleCreateNote = async () => {
    setIsCreating(true);
    // Simulating API POST delay
    await new Promise((resolve) => setTimeout(resolve, 500));
    const newId = Math.random().toString(36).substring(7);
    
    // REDIRECT: Ensure this path matches your folder [id]
    router.push(`/dashboard/documents/${newId}`);
    setIsCreating(false);
  };

  const filteredNotes = notes.filter((note) =>
    note.title.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div className="container mx-auto px-6 py-8 max-w-7xl">
      {/* --- HEADER --- */}
      <div className="flex flex-col md:flex-row md:items-center justify-between gap-6 mb-10">
        <div className="space-y-1">
          <div className="flex items-center gap-2 mb-2">
            <Badge variant="outline" className="rounded-full border-primary/30 text-primary bg-primary/5 px-3">
              <Sparkles className="h-3 w-3 mr-1" /> 2025 Workspace
            </Badge>
          </div>
          <h1 className="text-4xl font-bold tracking-tight bg-gradient-to-r from-foreground to-foreground/70 bg-clip-text">
            Your Hub
          </h1>
          <p className="text-muted-foreground text-md">
            Collaborate on documents and manage AI-powered notes.
          </p>
        </div>

        <div className="flex items-center gap-3">
           <Button variant="outline" size="lg" className="rounded-xl border-dashed">
             <Star className="h-4 w-4 mr-2" /> Favorites
           </Button>
           <Button 
             onClick={handleCreateNote} 
             disabled={isCreating}
             size="lg" 
             className="rounded-xl shadow-lg shadow-primary/20 gap-2 px-6"
           >
             <Plus className="h-5 w-5" /> 
             {isCreating ? "Creating..." : "New Note"}
           </Button>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">
        <div className="lg:col-span-8 space-y-8">
          <div className="flex flex-col sm:flex-row items-center justify-between gap-4 bg-muted/30 p-2 rounded-2xl border border-border/50">
            <Tabs defaultValue="all" className="w-full sm:w-auto">
              <TabsList className="bg-transparent gap-1">
                <TabsTrigger value="all" className="rounded-lg data-[state=active]:bg-background shadow-none">All Notes</TabsTrigger>
                <TabsTrigger value="recent" className="rounded-lg data-[state=active]:bg-background shadow-none">Recent</TabsTrigger>
                <TabsTrigger value="shared" className="rounded-lg data-[state=active]:bg-background shadow-none">Shared</TabsTrigger>
              </TabsList>
            </Tabs>

            <div className="relative w-full sm:w-64">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Find a document..."
                className="pl-10 bg-background border-none shadow-sm focus-visible:ring-1 focus-visible:ring-primary/50"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
          </div>

          <div className="grid gap-6 sm:grid-cols-2">
            {loading ? (
              Array.from({ length: 4 }).map((_, i) => (
                <Skeleton key={i} className="h-[200px] w-full rounded-3xl" />
              ))
            ) : filteredNotes.length > 0 ? (
              filteredNotes.map((note) => (
                <Link key={note.id} href={`/dashboard/documents/${note.id}`}>
                  <Card className="group relative h-full bg-background border-border/40 hover:border-primary/40 hover:shadow-2xl hover:shadow-primary/5 transition-all duration-500 rounded-3xl overflow-hidden">
                    <CardHeader className="pb-4">
                      <div className="flex justify-between items-start mb-2">
                        <div className="p-2.5 bg-primary/5 text-primary rounded-xl group-hover:bg-primary group-hover:text-primary-foreground transition-all duration-300">
                          <FileText className="h-5 w-5" />
                        </div>
                        <MousePointer2 className="h-4 w-4 text-muted-foreground opacity-0 group-hover:opacity-100 transition-all translate-x-2 group-hover:translate-x-0" />
                      </div>
                      <CardTitle className="text-xl font-semibold">{note.title}</CardTitle>
                      <CardDescription className="flex items-center gap-1.5 font-medium">
                        <Clock className="h-3 w-3" />
                        {note.updatedAt}
                      </CardDescription>
                    </CardHeader>
                    <CardContent>
                       <p className="text-sm text-muted-foreground line-clamp-2 leading-relaxed opacity-80 group-hover:opacity-100 transition-opacity">
                        {note.previewText}
                       </p>
                    </CardContent>
                  </Card>
                </Link>
              ))
            ) : (
              <div className="col-span-full py-24 text-center border-2 border-dashed rounded-3xl bg-muted/10">
                <Zap className="mx-auto h-12 w-12 text-muted-foreground/30 mb-4" />
                <h3 className="text-xl font-bold italic">Silence in the Hub...</h3>
              </div>
            )}
          </div>
        </div>

        {/* SIDEBAR */}
        <div className="lg:col-span-4 space-y-6">
          <Card className="rounded-3xl border-primary/10 bg-gradient-to-br from-primary/[0.03] to-transparent p-2">
            <CardHeader>
              <CardTitle className="text-sm font-bold flex items-center gap-2">
                <Zap className="h-4 w-4 text-primary fill-primary" />
                AI Activity
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
               <div className="p-4 bg-background rounded-2xl border border-primary/10 text-xs shadow-sm">
                 <p className="font-semibold text-primary mb-1">Weekly Insight</p>
                 Welcome to the mock dashboard!
               </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}